@import url('https://fonts.googleapis.com/css?family=Yesteryear&display=swap');

* {
    box-sizing: border-box;
}

@font-face {
    font-family: 'Liquid Crystal';
    src: url('./fonts/liquidcrystal-bold-webfont.woff2') format('woff2'),
         url('./fonts/liquidcrystal-bold-webfont.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

body {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #363635;
}

.metronome {
    width: 500px;
    background: #595A4A;
    border: 3px solid #8FBB99;
    padding: 30px;
    border-radius: 10px;
}

.metronome__title {
    font-family: 'Yesteryear', cursive;
    font-size: 68px;
    line-height: 1;
    color: #B0FE76;
}

.metronome__screen {
    font-family: 'Liquid Crystal';
    font-weight: bold;
    display: flex;
    align-items: center;
    background: #8FBB99;
    padding: 10px;
    box-shadow: inset 0 0 32px 0px rgba(0, 0, 0, 0.6);
    border-radius: 3px;
    margin: 10px 0 20px;
    font-size: 42px;
    border: 2px solid #363635;
    letter-spacing: 3px;
}

.metronome-controls {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.metronome-controls__input {
    background: #363635;
    border: none;
    padding: 10px;
    width: 90px;
    text-align: center;
    font-size: 33px;
    color: #8FBB99;
    border-radius: 3px;
    box-shadow: inset 0 0 32px 0px rgba(0, 0, 0, 0.6);
    font-family: 'Liquid Crystal';
}

.metronome-controls__button {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: #B0FE76;
    border: 5px solid #8FBB99;
    box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.9);
    cursor: pointer;
    transition: all .2s ease;
    outline: none !important;
    transform: translate(-5px, -5px);
    transform: translate3d(-5px, -5px, 0);
}

.metronome-controls__button.active {
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0);
    transform: translate(0, 0);
    transform: translate3d(0, 0, 0);
}

.metronome-controls__indicator {
    width: 20px;
    height: 20px;
    background: #363635;
    border-radius: 50%;
    display: inline-block;
    transition: all .3s ease;
}

.metronome-controls__indicator--main {
    background: #B0FE76;
}

.metronome-speaker {
    display: flex;
}

.metronome-speaker span {
    height: 75px;
    width: 15px;
    background: #363635;
    border-radius: 20px;
    margin: 0 10px;
    transform: rotate(-45deg);
    box-shadow: inset 0 0 10px 0px rgba(0, 0, 0, 0.6);
}